<template>
  <div class="address-content">
    <V-Scroll :isList="false" :scroll-height="`${mainHeight}px`">
      <div class="add-address">
        <router-link tag="p" :to="{path:'/myaccess/addressEdit'}">新增收货地址</router-link>
      </div>
      <ul class="address-list">
        <li v-for="(item,index) in addressLists" :key="index">
          <!-- swiper -->
          <swiper :options="swiperOption" ref="swiper">
            <swiper-slide class="content addressLefts">
              <div :class="{checkouteds:checksbg ==index}" @click="checkFun(index,item.id)">
                <p></p>
              </div>
              <div>
                <p>{{item.name}}</p>
                <p>{{item.address}}</p>
              </div>
              <router-link tag="div" :to="{path:'/myaccess/addressEdit',query:{id:item.id}}"><p></p></router-link>
            </swiper-slide>
            <swiper-slide class="menu" @click.native="deleteFun(index,item.id)">
              <div>
                删除
              </div>
            </swiper-slide>
          </swiper>
        </li>
      </ul>
    </V-Scroll>
  </div>
</template>

<script>
import Ajax from "@/mixins/ajax";
import { swiper, swiperSlide } from "vue-awesome-swiper";

/* eslint-disable */
import "swiper/dist/css/swiper.css";

const addressListUrl = "/tuser/list/address"
const deleteAddressUrl="/tuser/del/deleteAddress"
const chooseAddressUrl="/tuser/put/chooseAddress"

export default {
  name: "orderList",
  data() {
    return {
      menuCross: false,
      swiperOption: {
        initialSlide: 0,
        resistanceRatio: 0,
        slidesPerView: 'auto',
        slideToClickedSlide: true,
        on: {
          touchMove: () => {
            this.$refs.swiper.forEach(item => {
              item.swiper.slidePrev();
            });
          }
        }
      },
      addressLists:[
        {
          address: "朝阳北路与温榆河西路交叉口西150米新建村二期高层6号楼3单元1301",
          defaultFlag: 0,
          id: "67",
          name: "发绝地反",
          phone: "13681182959",
          region: "北京市北京市通州区",
          userId: "",
          zipCode: 101100
        },
        {
          address: "朝阳北路与温榆河西路交叉口西150米新建村二期高层6号楼3单元1301",
          defaultFlag: 0,
          id: "67",
          name: "张松辉",
          phone: "13681182959",
          region: "北京市北京市通州区",
          userId: "",
          zipCode: 101100
        },
        {
          address: "朝阳北路与温榆河西路交叉口西150米新建村二期高层6号楼3单元1301",
          defaultFlag: 0,
          id: "67",
          name: "电风扇",
          phone: "13681182959",
          region: "北京市北京市通州区",
          userId: "",
          zipCode: 101100
        },
        {
          address: "朝阳北路与温榆河西路交叉口西150米新建村二期高层6号楼3单元1301",
          defaultFlag: 0,
          id: "67",
          name: "是防守打法",
          phone: "13681182959",
          region: "北京市北京市通州区",
          userId: "",
          zipCode: 101100
        }
      ],
      checksbg:null
    };
  },
  computed: {
    swiper() {
      return this.$refs.swiper.swiper
    }
  },
  mixins: [Ajax],
  components: {
    swiper,
    swiperSlide
  },
  methods: {
    // 删除地址
    deleteFun: function(index,adderssid){
      this.get(deleteAddressUrl,{
        addressId:adderssid
      }).then(body=>{
        this.$refs.swiper[index] && this.$refs.swiper[index].swiper.slidePrev(false);
        this.addressLists.splice(index,1);
      })
    },
    // 选择用户地址
    checkFun(index,adderssid){
      this.checksbg =index;
      this.post(chooseAddressUrl,{
        userId:"",
        addressId:adderssid
      }).then(body=> {

      })
    },
    // 列表
    addressFun() {
      this.post(addressListUrl,{
        userId:""
      }).then(body=>{
        this.addressLists = body
      })
    }
  },
  mounted() {
    this.addressFun();
  },
};
</script>

<style scoped>
  .add-address{
    height: 1.48rem;
    line-height: 1.48rem;
    background-color: #fff;
    padding:0 .3rem;
    margin:.3rem auto;
  }
  .add-address >p {
    height: 1.48rem;
    font-size: .48rem;
    line-height: 1.48rem;
    color: #353535;
    padding-left:.6rem;
    background: url("../../images/userInfos/icon_add.png")no-repeat left center/.48rem .48rem,
                url("../../images/my/icon_right.png")no-repeat right center/.17rem .36rem;
  }
  .addressLefts{
    height: 2.46rem;
    background-color: #fff;
    margin-bottom:.3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:0 .3rem;
    box-sizing: border-box;
  }
  .addressLefts> div:nth-child(1)>p{
    width:1rem;
    height: 1rem;
  }
  div.checkouteds>p{
    background: url("../../images/userInfos/icon_addChecked.png") no-repeat center/.44rem .36rem;
  }
  .addressLefts> div:nth-child(2){
    margin:0 .1rem;
  }
  .addressLefts> div:nth-child(2)>p:nth-child(1){
    height: .6rem;
    font-size: .48rem;
    line-height: .6rem;
    color: #353535;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    overflow: hidden;
  }
  .addressLefts> div:nth-child(2)>p:nth-child(2){
    height: 1rem;
    font-size: .42rem;
    line-height: .5rem;
    color: #666666;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
  }
  .addressLefts> div:nth-child(3)>p{
    width:1rem;
    height:1rem;
    background: url("../../images/userInfos/icon_address.png") no-repeat center/.48rem .48rem;
  }

  .menu {
    width:2rem;
    height:2.46rem;
    line-height: 2.46rem;
    text-align: center;
    background-color: #de2245;
    color: #fff;
  }
</style>



